.navigationBlock {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  box-sizing: border-box;
  width: 390px;
  
 .navigation {
    background: #0D182D;
    color: #F9C40B;
    width: 190px;
    height: 70px;
    margin-bottom: 0px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-wrap: nowrap;
    text-decoration: none;
    font-size: 20px;
    box-sizing: border-box;
  }

  .navigation:link {
    color: #F9C40B;
  }
  .navigation:hover {
    background: #162032;
  }

  .navigationActive {
    background:  #0b214a;
    border-bottom: 2px solid #F9C40B;
    color: #F9C40B;
    width: 190px;
    height: 70px;
    margin-bottom: 0px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-wrap: nowrap;
    text-decoration: none;
    font-size: 20px;
    box-sizing: border-box;
  }
}

@media only screen and (max-width: 1200px)  {
  .navigationBlock {
    width: 280px;
  
    .navigation {
      width: 140px;
      font-size: 18px;
    }
    .navigationActive {
      width: 140px;
      font-size: 18px;
    }
  }
}

@media only screen and (max-width: 767px)  {
  .navigationBlock {
    width: 200px;

    .navigation {
      width: 100px;
      font-size: 16px;
    }
    .navigationActive {
      width: 100px;
      font-size: 16px;
    }
  }
}

@media only screen and (max-width: 450px)  {
  .navigationBlock {
    width: 160px;

    .navigation {
      width: 80px;
      font-size: 14px;
    }
    .navigationActive {
      width: 80px;
      font-size: 14px;
    }
  }
}

@media only screen and (min-width: 280px) and (max-width: 360px) {
  .navigationBlock {
    width: 140px;

    .navigation {
      width: 70px;
      font-size: 12px;
    }
    .navigationActive {
      width: 70px;
      font-size: 12px;
    }
  }
}